.location {
  display: flex;
  justify-content: space-around;
}
.locationComponent {
  background-color: #f0f2f2;
}
.location h4 {
  font-weight: 800;
  font-size: 19px;
}
.locationDiv {
  display: flex;
  flex-direction: column;
}
.locationDiv p {
  font-size: 14px;
  opacity: 0.6;
}
.fa-map-marker-alt {
  padding: 1px;
  color: rgb(204, 9, 9);
}

.discoverLocation p {
  text-align: left !important;
}

.discoverLocation h1 {
  font-weight: 800;
  margin-bottom: 30px;
}
.btn-danger {
  margin-top: 30px;
  width: 200px;
}

@media screen and (max-width: 768px) {
  .locationDiv {
    margin-top: 30px;
  }
  .location h4 {
    margin-top: 2px;
    font-weight: 800;
    font-size: 12px;
  }
  .locationDiv p {
    font-size: 11px;
    opacity: 0.6;
    margin-top: -10px;
    margin-left: 10px !important;
  }
  .fa-map-marker-alt {
    padding: 0px !important;
    color: rgb(204, 9, 9);
  }
}

@media screen and (max-width: 500px) {
  .locationDiv {
    margin-top: 0px;
  }
  .location h4 {
    margin-top: 2px;
    font-weight: 800;
    font-size: 10px;
  }
  .locationDiv p {
    font-size: 10px !important;
    opacity: 0.6;
    margin-top: -3px;
    // margin-left: 10px !important;
  }
  .fa-map-marker-alt {
    font-size: 16px;
    padding: 2px !important;
    color: rgb(204, 9, 9);
  }
}

@media screen and (max-width: 320px) {
  .locationDiv {
    margin-top: 0px;
    margin-left: -20px;
    margin-right: -50px;
  }
  .location h4 {
    margin-top: 2px;
    font-weight: 800;
    margin-left: -55px;
    font-size: 8px;
  }
  .locationDiv p {
    font-size: 8px;
    opacity: 0.6;
    margin-top: -3px;
    margin-left: 30px !important;
  }
  .fa-map-marker-alt {
    font-size: 16px;
    padding: 0px !important;
    color: rgb(204, 9, 9);
  }
}

.discoverLocation {
  h1 {
    text-align: left !important;
  }
}
